import React, { Component } from 'react';
import classes from './CreateImage.module.css';
import axios from 'axios';

class CreateImagePage extends Component{
  
  constructor(props) {
    super(props);
    this.state = { 
      imageData:{
        "name":"",
        "url":"",
        "details":""
      }
     }
  }

  

  handleChange = (e) => {
    let key = e.target.name;
    let tempState = {
      ...this.state,

    }
    tempState['imageData'][key]=e.target.value;
    this.setState({tempState})
  }

  handleSubmit = (e) => {
    e.preventDefault();
    console.log(this.state.imageData)
    //axios.post('http://localhost:9000/add')
    axios({
      method: 'post',
      url:"/add",
      headers: {}, 
      data: {
        ...this.state.imageData
      }
    }).then(()=>{
      alert("Image Added Successfuly")
      
      this.props.history.push('/')
    });
  }

  render() { 
   
    return ( 
      <div className={classes.formWrapper}>
        <h1>Add a new image here...</h1>
        <form className={classes.addImageForm} onSubmit={this.handleSubmit}>
          <input type="text" required placeholder="Enter Image Name" value={this.state.imageData.name} name="name"onChange={this.handleChange} ></input>
          <br></br>
          <input type="text" required placeholder="Enter Image Url" value={this.state.imageData.url} name="url" onChange={this.handleChange}></input>
          <br></br>
          <input type="text" required placeholder="Enter Image Details" value={this.state.imageData.details} name="details" onChange={this.handleChange}></input>
          <br></br>
          <input style={{cursor:"pointer"}} type="submit" value="ADD TO GALLERY"></input>
        </form>
      </div>
    )
  }
}

export default CreateImagePage;