import React from 'react';
import classes from './DetailsImage.module.css';
import { useEffect,useState} from 'react';
import axios from 'axios';
import Button from '@material-ui/core/Button';
import LinearProgress from '@material-ui/core/LinearProgress';
import {useHistory} from 'react-router-dom';


const DetailImage = (props) => {
    const history = useHistory();

    const [dataToShow,setDataToShow] = useState([]);
    const [showLoader,setShowLoader] = useState(true);

    useEffect(()=>{
        console.log(window.location.pathname)
        axios.get(`${window.location.pathname}`).then((res)=>{
            console.log(res.data)
            setDataToShow(res.data);
            setShowLoader(false);
        })
        
    },[]);
    return(
        <> 
      
        <div className={classes.mainContainer}>
            <h1>Here is the Image Detail</h1>
            {showLoader?<LinearProgress/>:
            <div>
                <img src={dataToShow.url} alt="details_image"></img>
                <h1>{dataToShow.name}</h1>

                <p>{dataToShow.details}</p>
            </div>}
            <Button onClick={()=>{
               
                history.push('/')
            }} variant="outlined" color="primary">
                GO BACK
            </Button>
        </div>
        </>
    )
}
export default DetailImage;